body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container,.order{width:75%;margin:0 auto}img{user-select:none}h1,h2,h3,h4{font-family:"Raleway",sans-serif}p,li{font-family:"Roboto",sans-serif}button,a{cursor:pointer}ul{list-style:none;margin-block-start:0;margin-block-end:0;padding-inline-start:0}.dateWrapper .date{width:40px;height:45px;border-radius:1px;background-color:#408DE4;color:#FFF;text-transform:uppercase;font-family:"Roboto",sans-serif;user-select:none;display:flex;flex-direction:column;justify-content:center;align-items:center;font-weight:bold}.dateWrapper .date .day{font-size:18px;margin-block-start:0;margin-block-end:0}.dateWrapper .date .month{font-size:11px;margin-block-end:0;margin-block-start:0}.order .title h3{text-transform:uppercase;font-size:20px;color:#222222}.order .characteristics .characteristic{border:1px solid #E8E8E8;padding:22px 340px 22px 25px;display:flex;justify-content:space-between}.order .characteristics .characteristic .characteristic__title{color:#222222;text-transform:uppercase;font-size:14px;font-weight:bold;font-family:"Raleway",sans-serif}.order .characteristics .characteristic .characteristic__text{color:#666666;font-size:14px}.order .characteristics .characteristic .characteristic__price{color:#222222;font-size:14px;font-weight:500}.order .characteristics .characteristic .shippingContent .shippingContent__choosing{display:flex;align-items:center}.order .btn_placeOrder{margin:20px auto}.order .btn_placeOrder button{outline:none;border:none;color:#FFF;text-transform:uppercase;padding:20px 30px;font-size:15px;font-weight:900;font-family:"Raleway",sans-serif;background-color:#408DE4}.order .btn_placeOrder button:hover{transition:all .4s linear;color:#FFAE54}
