body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container,.loginPanel{width:75%;margin:0 auto}img{user-select:none}h1,h2,h3,h4{font-family:"Raleway",sans-serif}p,li{font-family:"Roboto",sans-serif}button,a{cursor:pointer}ul{list-style:none;margin-block-start:0;margin-block-end:0;padding-inline-start:0}.dateWrapper .date{width:40px;height:45px;border-radius:1px;background-color:#408DE4;color:#FFF;text-transform:uppercase;font-family:"Roboto",sans-serif;user-select:none;display:flex;flex-direction:column;justify-content:center;align-items:center;font-weight:bold}.dateWrapper .date .day{font-size:18px;margin-block-start:0;margin-block-end:0}.dateWrapper .date .month{font-size:11px;margin-block-end:0;margin-block-start:0}.vertical_line{color:#4F90D8;margin:0px 20px}.slash{color:#fff;margin:0px 4px}.iconMargin{margin:0px 5px}.loginPanelWrapper{width:100%;background-color:#3B83D4;height:42px}.loginPanelWrapper p{color:#fff;font-weight:500;font-size:12px;cursor:pointer}.loginPanel{display:flex;justify-content:space-between;align-items:center}.login{display:flex;align-items:center}.login_btn,.signup{text-decoration:none}.helpPanel{display:flex;align-items:center}.helpPanel .wishlist{display:flex;align-items:center}.helpPanel .wishlist a{text-decoration:none;color:#fff;font-size:12px;font-weight:bold;cursor:pointer}.helpPanel .language{display:flex;align-items:center;cursor:pointer}.helpPanel .language .sortDownIcon{color:#FFF;margin-bottom:5px}
