body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container{width:75%;margin:0 auto}img{user-select:none}h1,h2,h3,h4{font-family:"Raleway",sans-serif}p,li{font-family:"Roboto",sans-serif}button,a{cursor:pointer}ul{list-style:none;margin-block-start:0;margin-block-end:0;padding-inline-start:0}.dateWrapper .date{width:40px;height:45px;border-radius:1px;background-color:#408DE4;color:#FFF;text-transform:uppercase;font-family:"Roboto",sans-serif;user-select:none;display:flex;flex-direction:column;justify-content:center;align-items:center;font-weight:bold}.dateWrapper .date .day{font-size:18px;margin-block-start:0;margin-block-end:0}.dateWrapper .date .month{font-size:11px;margin-block-end:0;margin-block-start:0}.cartPopupWrapper{width:310px;position:absolute;background-color:#FFF;padding:25px;top:70px;left:-145px;user-select:none;z-index:10}.cartPopupWrapper .cartPopup .products .product{display:flex;border-bottom:1px solid #EEEEEE;padding:10px 0px;width:270px;cursor:pointer}.cartPopupWrapper .cartPopup .products .product:hover{transition:all .5s ease;border-bottom:1px solid #222222}.cartPopupWrapper .cartPopup .products .product .image{position:relative;display:flex}.cartPopupWrapper .cartPopup .products .product .image .delete{position:absolute;background-color:#fff;text-align:center;z-index:999;border:1px solid #E6E6E6;top:-9px;left:-5px;width:18px;height:18px;border-radius:50px;color:#505050;cursor:pointer}.cartPopupWrapper .cartPopup .products .product .image .delete:hover{transition:all .1s linear;border:1px solid #777777}.cartPopupWrapper .cartPopup .products .product .image .productImage{width:72px;height:78px}.cartPopupWrapper .cartPopup .products .product .productInfo{padding:0px 16px}.cartPopupWrapper .cartPopup .products .product .productInfo .title{color:#444444;font-size:14px}.cartPopupWrapper .cartPopup .products .product .productInfo h5{color:#222222;font-family:"Roboto",sans-serif;font-size:14px}.cartPopupWrapper .cartPopup .total{display:flex;align-items:center;justify-content:space-between;width:270px}.cartPopupWrapper .cartPopup .total h4{text-transform:uppercase;color:#222222;font-size:12px;font-family:"Roboto",sans-serif}.cartPopupWrapper .cartPopup .total h5{color:#222222;font-size:14px;font-family:"Roboto",sans-serif;font-weight:500}.cartPopupWrapper .cartPopup .enterCartPage{display:flex;align-items:center;justify-content:space-between}.cartPopupWrapper .cartPopup .enterCartPage .enterCartPage__btns a{text-decoration:none;padding:16px 28px;border:1px solid #E8E8E8;font-size:12px;font-family:"Raleway",sans-serif;color:#191A1C;font-weight:600;border-radius:1px;text-transform:uppercase}.cartPopupWrapper .cartPopup .enterCartPage .enterCartPage__btns a:hover{transition:all .4s linear;color:#FFF;background-color:#408DE4;border:1px solid #408DE4}
