body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container{width:75%;margin:0 auto}img{user-select:none}h1,h2,h3,h4{font-family:"Raleway",sans-serif}p,li{font-family:"Roboto",sans-serif}button,a{cursor:pointer}ul{list-style:none;margin-block-start:0;margin-block-end:0;padding-inline-start:0}.dateWrapper .date{width:40px;height:45px;border-radius:1px;background-color:#408DE4;color:#FFF;text-transform:uppercase;font-family:"Roboto",sans-serif;user-select:none;display:flex;flex-direction:column;justify-content:center;align-items:center;font-weight:bold}.dateWrapper .date .day{font-size:18px;margin-block-start:0;margin-block-end:0}.dateWrapper .date .month{font-size:11px;margin-block-end:0;margin-block-start:0}.product{display:flex;padding:20px 30px;border:1px solid #E8E8E8;align-items:center;cursor:pointer}.product .item{display:flex;align-items:center;width:600px}.product .item img{width:55px;height:60px;border-radius:1px;margin-right:20px}.product .item p{color:#666666;font-size:14px}.product .price{width:175px}.product .price h4{color:#222222;font-size:14px;font-family:"Roboto",sans-serif}.product .isStockStatus{width:150px}.product .isStockStatus .InStock{color:#88A36C;text-transform:uppercase;font-size:12px;font-weight:500}.product .isStockStatus .OutOfStock{color:#232323;font-size:12px;font-weight:500;text-transform:uppercase}.product .btn_addToCart button{outline:none;border:none;border-radius:1px;color:#fff;background-color:#232323;text-transform:uppercase;font-family:"Raleway",sans-serif;font-size:12px;font-weight:600;padding:15px 20px}.product .btn_addToCart button:hover{transition:all .3s ease;color:#D3CAFF}.product .btn_addToCart .withoutButton{height:30px;width:117px}.product .btn_remove{margin-left:30px}.product .btn_remove img{cursor:pointer}
