body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container{width:75%;margin:0 auto}img{user-select:none}h1,h2,h3,h4{font-family:"Raleway",sans-serif}p,li{font-family:"Roboto",sans-serif}button,a{cursor:pointer}ul{list-style:none;margin-block-start:0;margin-block-end:0;padding-inline-start:0}.dateWrapper .date{width:40px;height:45px;border-radius:1px;background-color:#408DE4;color:#FFF;text-transform:uppercase;font-family:"Roboto",sans-serif;user-select:none;display:flex;flex-direction:column;justify-content:center;align-items:center;font-weight:bold}.dateWrapper .date .day{font-size:18px;margin-block-start:0;margin-block-end:0}.dateWrapper .date .month{font-size:11px;margin-block-end:0;margin-block-start:0}.form .formItem p{color:#666666;font-size:14px}.form .formItem input{height:45px;width:555px;border:1px solid #E8E8E8}.form .formItem span{color:#FC4242}.form .formItem textarea{border:1px solid #E8E8E8;width:555px;height:150px;resize:none;border-radius:1px}.form .btn_sendMessage{margin:20px 0px}.form .btn_sendMessage button{padding:18px 24px;outline:none;border:none;border-radius:1px;background-color:#408DE4;color:#FFF;text-transform:uppercase;font-family:"Raleway",sans-serif;font-size:12px;font-weight:bold}.form .btn_sendMessage button:hover{transition:all .4s ease-in;color:#AE88DD;border-radius:25px}
