body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container,.welcome{width:75%;margin:0 auto}img{user-select:none}h1,h2,h3,h4{font-family:"Raleway",sans-serif}p,li{font-family:"Roboto",sans-serif}button,a{cursor:pointer}ul{list-style:none;margin-block-start:0;margin-block-end:0;padding-inline-start:0}.dateWrapper .date{width:40px;height:45px;border-radius:1px;background-color:#408DE4;color:#FFF;text-transform:uppercase;font-family:"Roboto",sans-serif;user-select:none;display:flex;flex-direction:column;justify-content:center;align-items:center;font-weight:bold}.dateWrapper .date .day{font-size:18px;margin-block-start:0;margin-block-end:0}.dateWrapper .date .month{font-size:11px;margin-block-end:0;margin-block-start:0}.welcome{display:flex;align-items:center;justify-content:center;height:512px;background-color:#FFFF}.welcome>div{margin:0px 40px}.storeImg img{width:550px;height:350px}.welcome__text{display:flex;flex-direction:column;align-items:start}.welcome__text .welcome__text_title{font-weight:600;color:#222222;font-size:24px}.welcome__text .welcome__text_subTitle{color:#7B7B7B;font-size:15px}.welcome__text .welcome__text_maintext{font-size:14px;color:#8D8D8D}.welcome__text .btn_contactUs{margin:30px 0px}.welcome__text .btn_contactUs a{text-decoration:none;border:1px solid #E5E5E5;background:#EEEEEE;text-transform:uppercase;font-family:"Raleway",sans-serif;font-size:12px;color:#222222;padding:16px 28px}.welcome__text .btn_contactUs a:hover{opacity:0.9}@media screen and (max-device-width: 1024px){.welcome{width:840px}.welcome>div{margin:0px 20px}.storeImg img{width:400px;height:300px}.welcome__text .welcome__text_title{font-weight:600;color:#222222;font-size:16px}.welcome__text .welcome__text_subTitle{color:#7B7B7B;font-size:12px}.welcome__text .welcome__text_maintext{font-size:12px;color:#8D8D8D}.welcome__text .btn_contactUs a{font-size:10px}}
