body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container{width:75%;margin:0 auto}img{user-select:none}h1,h2,h3,h4{font-family:"Raleway",sans-serif}p,li{font-family:"Roboto",sans-serif}button,a{cursor:pointer}ul{list-style:none;margin-block-start:0;margin-block-end:0;padding-inline-start:0}.dateWrapper .date{width:40px;height:45px;border-radius:1px;background-color:#408DE4;color:#FFF;text-transform:uppercase;font-family:"Roboto",sans-serif;user-select:none;display:flex;flex-direction:column;justify-content:center;align-items:center;font-weight:bold}.dateWrapper .date .day{font-size:18px;margin-block-start:0;margin-block-end:0}.dateWrapper .date .month{font-size:11px;margin-block-end:0;margin-block-start:0}.navLink{text-decoration:none;color:#222222}.bestseller{position:relative;display:flex;align-items:center;border:1px solid #E8E8E8;text-decoration:none;cursor:pointer}.bestseller .new,.bestseller .sale{position:absolute;top:10%;left:5%;width:42px;height:20px;border-radius:1px;color:#FFF;font-weight:bold;font-size:12px;display:flex;align-items:center;justify-content:center;z-index:999}.bestseller .new{background-color:#F7AB27}.bestseller .sale{background-color:#EA5C5C}.bestseller .bestseller__photo{width:190px;height:190px;z-index:888;margin:10px}.bestseller .bestseller__content{margin:0px 20px}.bestseller .bestseller__content .bestseller__title{font-family:"Roboto",sans-serif;font-size:14px;font-weight:normal;color:#444444;min-height:50px}.bestseller .bestseller__content .price{color:#222222;font-size:14px}.bestseller .bestseller__content .rating{display:flex;margin-bottom:20px}.bestseller .bestseller__content .rating img{margin:0 2px}.bestseller .bestseller__content .bestseller__btn button{outline:none;border:1px solid #E5E5E5;color:#222222;text-transform:uppercase;padding:12px;border-radius:1px;font-weight:600;font-family:"Raleway",sans-serif}
